<template src="./template.html"></template>
<style src="./style.css"></style>
<script>
    export default {
        name: "SimpleWindow",
        props: {
            windowId: String,
            title: { type: String, default: '–û–∫–Ω–æ' },
            appName: { type: String, default: 'app' },
            isMinimized: { type: Boolean, default: false }
        },
        emits: ['close', 'minimize', 'focus', 'restore'],
        data() {
            return {
                posX: 100,
                posY: 100,
                isDragging: false,
                dragOffset: { x: 0, y: 0 }
            }
        },
        computed: {
            isVisible() { return !this.isMinimized; },
            
            windowStyle() {
                return {
                    left: `${this.posX}px`,
                    top: `${this.posY}px`
                }
            }
        },
        mounted() {
            // –î–æ–±–∞–≤–ª—è–µ–º –≥–ª–æ–±–∞–ª—å–Ω—ã–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ –¥–ª—è –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏—è
            document.addEventListener('mousemove', this.onDrag);
            document.addEventListener('mouseup', this.stopDrag);
        },
        beforeUnmount() {
            // –£–±–∏—Ä–∞–µ–º –≥–ª–æ–±–∞–ª—å–Ω—ã–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ –ø—Ä–∏ —É–Ω–∏—á—Ç–æ–∂–µ–Ω–∏–∏ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
            document.removeEventListener('mousemove', this.onDrag);
            document.removeEventListener('mouseup', this.stopDrag);
        },
        methods: {
            focusWindow(e) {
                this.$emit('focus', this.windowId);
      
                // –ù–∞—á–∏–Ω–∞–µ–º –ø–µ—Ä–µ—Ç–∞—Å–∫–∏–≤–∞–Ω–∏–µ, –µ—Å–ª–∏ –∫–ª–∏–∫–Ω—É–ª–∏ –Ω–∞ –∑–∞–≥–æ–ª–æ–≤–æ–∫
                if (e.target.closest('.window-header')) {
                    this.startDrag(e);
                }
            },
            
            startDrag(e) {
                this.isDragging = true
                this.dragOffset = {
                    x: e.clientX - this.posX,
                    y: e.clientY - this.posY
                }
            },
            
            onDrag(e) {
                if (!this.isDragging) return;
      
                this.posX = e.clientX - this.dragOffset.x;
                this.posY = e.clientY - this.dragOffset.y;
      
                // –û–≥—Ä–∞–Ω–∏—á–∏–≤–∞–µ–º –ø–µ—Ä–µ–º–µ—â–µ–Ω–∏–µ –≤ –ø—Ä–µ–¥–µ–ª–∞—Ö —ç–∫—Ä–∞–Ω–∞
                const maxX = window.innerWidth - 300;
                const maxY = window.innerHeight - 200;
      
                this.posX = Math.max(0, Math.min(this.posX, maxX));
                this.posY = Math.max(0, Math.min(this.posY, maxY));
            },
    
            stopDrag() { this.isDragging = false; },
    
            minimize() { this.$emit('minimize', this.windowId); },
    
            maximize() { console.log('–ú–∞–∫—Å–∏–º–∏–∑–∏—Ä–æ–≤–∞—Ç—å –æ–∫–Ω–æ', this.windowId); },
    
            close() { this.$emit('close', this.windowId); },
        }
    }
</script>